//配置路由
import Vue from 'vue'
import VueRouter from 'vue-router'
//使用插件
Vue.use(VueRouter)

//引入路由组件
import Login from '@/pages/Login'
import Home from '@/pages/Home'

export default new VueRouter({
    routes: [{
            path: '/login',
            component: Login
        },
        {
            path: '/home',
            component: Home
        },
        {
            path: '*',
            redirect: '/login'
        }
    ]
})